import './App.css';
import React, { useState, useEffect } from 'react';
import { Bell, Smartphone, CheckCircle, AlertCircle } from 'lucide-react';
import { generateToken, onMessageListener } from './firebase';

const FirebasePWAApp = () => {
  const [token, setToken] = useState('');
  const [status, setStatus] = useState('');
  const [isSupported, setIsSupported] = useState(false);
  const [permission, setPermission] = useState('default');

  useEffect(() => {
    // Check if the browser supports notifications
    if ('Notification' in window && 'serviceWorker' in navigator) {
      setIsSupported(true);
      setPermission(Notification.permission);
    }
  }, []);
useEffect(() => {
  const unsubscribe = onMessageListener().then(payload => {
    console.log("Message received. ", payload);
    
    const { title, body } = payload.notification;

    // Manually show notification
    new Notification(title, {
      body,
      icon: '/icon-192x192.png' // make sure this path is correct
    });
  });

  return () => {
    // cleanup if necessary
  };
}, []);

  const clickGenerateToken = async () => {
  try {
    setStatus('Generating token...');
    
    const permission = await Notification.requestPermission();
    setPermission(permission);
    
    if (permission === 'granted') {
      const mockToken = await generateToken(); //  Await here
      setToken(mockToken);
      setStatus('Token generated successfully!');
      
      new Notification('Firebase PWA', {
        body: mockToken,
        icon: '/icon-192x192.png'
      });
    } else {
      setStatus('Notification permission denied');
    }
  } catch (error) {
    setStatus(`Error: ${error.message}`);
  }
};

  const testNotification = () => {
    if (permission === 'granted') {
      new Notification('Test Notification', {
        body: 'This is a test notification from your PWA!',
        icon: '/icon-192x192.png'
      });
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-600 via-blue-600 to-indigo-700 p-4">
      <div className="max-w-md mx-auto bg-white rounded-2xl shadow-2xl overflow-hidden">
        {/* Header */}
        <div className="bg-gradient-to-r from-purple-500 to-blue-500 p-6 text-white text-center">
          <Bell className="w-12 h-12 mx-auto mb-3 animate-pulse" />
          <h1 className="text-2xl font-bold">Firebase PWA</h1>
          <p className="text-purple-100 mt-1">Push Notification App</p>
        </div>

        {/* Content */}
        <div className="p-6 space-y-6">
          {/* Support Status */}
          <div className="flex items-center space-x-3 p-4 bg-gray-50 rounded-lg">
            <Smartphone className="w-6 h-6 text-blue-500" />
            <div>
              <p className="font-medium text-gray-800">PWA Support</p>
              <p className="text-sm text-gray-500">
                {isSupported ? 'Supported ' : 'Not Supported '}
              </p>
            </div>
          </div>

          {/* Permission Status */}
          <div className="flex items-center space-x-3 p-4 bg-gray-50 rounded-lg">
            {permission === 'granted' ? (
              <CheckCircle className="w-6 h-6 text-green-500" />
            ) : (
              <AlertCircle className="w-6 h-6 text-orange-500" />
            )}
            <div>
              <p className="font-medium text-gray-800">Notification Permission</p>
              <p className="text-sm text-gray-500 capitalize">{permission}</p>
            </div>
          </div>

          {/* Generate Token Button */}
          <button
            onClick={clickGenerateToken}
            disabled={!isSupported}
            className="w-full bg-gradient-to-r from-purple-500 to-blue-500 text-white py-4 px-6 rounded-xl font-semibold text-lg shadow-lg hover:shadow-xl transform hover:scale-105 transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed disabled:transform-none"
          >
            Generate FCM Token
          </button>

          {/* Test Notification Button */}
          {permission === 'granted' && (
            <button
              onClick={testNotification}
              className="w-full bg-gradient-to-r from-green-500 to-emerald-500 text-white py-3 px-6 rounded-xl font-semibold shadow-lg hover:shadow-xl transform hover:scale-105 transition-all duration-200"
            >
              Test Notification
            </button>
          )}

          {/* Status Message */}
          {status && (
            <div className="p-4 bg-blue-50 border border-blue-200 rounded-lg">
              <p className="text-blue-800 text-sm font-medium">{status}</p>
            </div>
          )}

          {/* Token Display */}
          {token && (
            <div className="p-4 bg-gray-50 rounded-lg">
              <p className="text-sm font-medium text-gray-700 mb-2">FCM Token:</p>
              <div className="bg-white p-3 rounded border text-xs font-mono text-gray-600 break-all">
                {token}
              </div>
              <button
                onClick={() => navigator.clipboard.writeText(token)}
                className="mt-2 text-blue-500 text-sm hover:text-blue-600"
              >
                Copy Token
              </button>
            </div>
          )}

          {/* Instructions */}
          <div className="text-xs text-gray-500 space-y-2">
            <p> Click "Generate FCM Token" to enable notifications</p>
            <p> Allow notification permission when prompted</p>
            <p> Use the token for server-side push notifications</p>
            <p> Add to home screen for full PWA experience</p>
          </div>
        </div>
      </div>
    </div>
  );
};

// Export only the FirebasePWAApp component as default
export default FirebasePWAApp;